#banner {
    background-color: rgb(18, 68, 36);
    color: rgb(240, 240, 240);
    border-bottom: black;
}

.logo {
    font-size: 55px;
    padding-top: 35px;
    
    font-family: 'Graduate', cursive;
    
}
