font-family: 'Graduate', cursive; */
body {
    font-family: 'Martel', serif;
    background-image: url("data:image/jpeg;base64,");
    background-size: cover;
    color: rgb(240, 240, 240);
}

.duck {
    height: 135px;
    width: 135px;
    float: left;
    border-radius: 230px;
}
/* https://ak.picdn.net/offset/photos/570d6723ba6f6bfc1a47de0a/medium/photo.jpg */
#welcomeTable {
    text-align: center;
    border-style: solid;
    color: rgb(236, 236, 236);
    border-color: rgba(17, 17, 17, 0);
    /* background-color: rgba(0, 0, 0, 0.507); */
    background-image: url("https://ak.picdn.net/offset/photos/570d6723ba6f6bfc1a47de0a/medium/photo.jpg");
    background-color: black;
    background-size: cover;
    padding-bottom: 0px;
    border-radius: 5px;
    padding: 100px;
    
}

.welcome {
    font-size: 25px;
    border-radius: 10px;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.705);
    color: white;
}

.studentButton {
    float: right;
    padding-left: 5px;
    padding-right: 20px;
    padding-top: 25px;
    font-size: 30px;
    margin-top: 25px;
    color: rgb(236, 236, 236);
    cursor: pointer;
    
    border-radius: 15px;
    
    text-align: center;
}

.todd {
    height:200px;
    width: 200px;
    border-radius: 10px;
    position: center;
}


hr {
    background-color: rgb(19, 80, 41);
    height: 4px;    
}


#text-box {
    background-size: cover;
    padding: 100px;
    margin-top: 0px;  
}

.black {
    background-color: rgba(0, 0, 0, 0.116);  
}

.edSupport {
    text-align: center;
    font-size: 35px;
}

#staffTable {
    background-color: rgb(18, 68, 36);
    border-radius: 10px;
}

.staffHr {
    background-color: white;
    height: 1px;
}

.staffTitle {
    text-align: center;
}

.todd {
    font-size: 35px;
    cursor: hand;
}

.ta {
    font-size: 35px;
}

a {
    text-decoration: none;
    color: white;
    cursor: hand;
}






















